.chart-container {
  position: relative;
  width: 100%;
  max-width: 800px;
  height: 500px;
  margin: auto;
}

.selectors {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.company-selector label,
.month-selector label {
  margin-right: 10px;
}

.legend {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

.legend-item {
  display: flex;
  align-items: center;
  margin: 0 10px;
}

.legend-color {
  width: 15px;
  height: 15px;
  margin-right: 5px;
  background-color: #e41a1c;
}

.legend-color.open {
  background-color: #b2df8a;
}

.tooltip {
  position: absolute;
  background-color: white;
  border: 1px solid #ddd;
  padding: 8px;
  border-radius: 4px;
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.2s ease;
}

